@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Noto+Sans:wght@400;700&display=swap);
body{margin:0;font-family:'Noto Sans JP', 'Noto Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:11pt;color:#616161}body h1{color:#3958a7}body h2{color:#3958a7}code{font-family:'Source Code Pro', Menlo, Monaco, Consolas, 'Courier New', monospace;font-size:12pt}

.card{margin-top:3rem;margin-bottom:3rem;padding:3rem;box-shadow:10px 10px 14px -9px #757575;border-radius:12px}.container{width:90vw;border:solid 1px rgba(230,230,230,0.548)}body{overflow-x:hidden}

.navbar{padding-top:1rem;padding-bottom:1rem;background:#162447}.navbar .branding{letter-spacing:1rem;color:#e43f5a;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;font-weight:bold;vertical-align:middle;display:table;-webkit-text-orientation:upright;text-orientation:upright;padding-top:1rem}.navbar .branding:hover{cursor:pointer}.navbar a{color:#ebebeb;border:solid 1px transparent;margin-left:1rem;margin-right:1rem;width:100px;text-align:center}.navbar a:hover{color:#e43f5a;border:solid 1px #e43f5a;transition:border-left .15s, border-top .15s .15s, border-right .15s .3s, border-bottom .15s .45s}

.author-pic-wrapper{display:flex;justify-content:center;align-items:center}.author-pic-wrapper .author-pic{height:150px;width:150px;background-size:cover;border-radius:50%;box-shadow:-10px 10px 14px -9px #757575}

.projects .row{border-bottom:solid 1px #b1b1b1;align-items:center;padding-bottom:1rem}.projects .row .col{padding:1rem 1rem}@media (max-width: 600px){.container{max-width:100%;width:100vw;margin:2rem 0;border-radius:0}.card{width:100vw;margin:2rem 0}}

.quickies{display:flex;justify-content:center;align-items:center;text-align:center;color:#3fe4bb}.quickies .material-icons{color:#162447}


.splash-wrapper{display:flex;text-align:center;align-items:center;flex-direction:column;margin:0;padding:0;width:100vw;height:100vh;background:#0c1b3f;color:#e43f5a}.splash-wrapper .splash{display:block;font-size:20pt;margin:2rem 0}.splash-wrapper .splash:hover{cursor:default}.splash-wrapper .scroll-down{position:absolute;bottom:5%;color:#00e498}.splash-wrapper .scroll-down h1{font-size:16pt}

